/* Navigation Styles */
.navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 1rem 0;
  z-index: 1000;
}

.nav-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}

.nav-logo {
  display: flex;
  align-items: center;
}

.logo-image {
  height: 120px;
  width: auto;
  transition: opacity 0.3s ease;
}

.logo-image:hover {
  opacity: 0.8;
}

.nav-list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 2rem;
}

.nav-list li a {
  color: #7070FF;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-list li a:hover {
  color: #ffffff;
}

/* Existing Portfolio Styles */
.portfolio-container {
  width: 100%;
  max-width: 100vw;
  overflow-x: hidden;
  padding-top: 80px;
  min-height: 100vh;
  background-color: #000000;
  color: #ffffff;
}

.portfolio-container::before,
.portfolio-container::after {
  content: '';
  position: fixed;
  width: 600px;
  height: 600px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(112, 112, 255, 0.15) 0%, transparent 70%);
  filter: blur(60px);
  z-index: -1;
  pointer-events: none;
}

.portfolio-container::before {
  top: -200px;
  left: -200px;
}

.portfolio-container::after {
  bottom: -200px;
  right: -200px;
  background: radial-gradient(circle, rgba(112, 112, 255, 0.1) 0%, transparent 70%);
}

.hero-section {
  height: 120vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 20px;
  background-color: #000000;
  position: relative;
  z-index: 1;
}

.name {
  font-size: 4rem;
  margin-bottom: 1rem;
  animation: fadeInUp 1s ease;
  color: #7070FF;
}

.title {
  font-size: 1.8rem;
  color: #ffffff;
  animation: fadeInUp 1s ease 0.3s;
  opacity: 0;
  animation-fill-mode: forwards;
}

/* Add a subtle fade-in animation for sections */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* About Section Styles */
.about-section,
.skills-section,
.education-section,
.projects-section,
.certifications-section,
.contact-section {
  max-width: 1200px;
  width: 90%;
  margin: 4rem auto;
  padding: 2rem;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.section-title {
  color: #7070FF;
  font-size: 2rem;
  margin-bottom: 2rem;
  text-align: center;
}

.about-content {
  display: flex;
  gap: 4rem;
  align-items: center;
  justify-content: center;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
}

.profile-image-container {
  flex: 0 0 auto;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(112, 112, 255, 0.3);
  border: 3px solid rgba(112, 112, 255, 0.2);
}

.profile-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.profile-image-container:hover .profile-image {
  transform: scale(1.05);
}

.about-text-container {
  flex: 1;
  max-width: 600px;
  padding: 0 1rem;
}

.about-text {
  color: #ffffff;
  font-size: 1.1rem;
  line-height: 1.8;
  text-align: justify;
  margin: 0 0 2rem 0;
}

.social-links {
  display: flex;
  gap: 1.5rem;
  margin-top: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: #7070FF;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  background-color: rgba(112, 112, 255, 0.1);
}

.social-link:hover {
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

.social-icon {
  font-size: 1.5rem;
}

/* Skills Section Styles */
.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
  justify-content: center;
  margin-top: 2rem;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  padding: 1.5rem;
  background-color: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  transition: all 0.3s ease;
  width: 120px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.skill-item:hover {
  transform: translateY(-5px);
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1);
}

.skill-icon {
  font-size: 3rem;
  transition: color 0.3s ease;
}

/* Original brand colors for each technology */
.skill-item:nth-child(1) .skill-icon { /* Java */
  color: #f89820;
}

.skill-item:nth-child(2) .skill-icon { /* React */
  color: #61DAFB;
}

.skill-item:nth-child(3) .skill-icon { /* JavaScript */
  color: #F7DF1E;
}

.skill-item:nth-child(4) .skill-icon { /* HTML */
  color: #E34F26;
}

.skill-item:nth-child(5) .skill-icon { /* CSS */
  color: #1572B6;
}

.skill-item:nth-child(6) .skill-icon { /* Python */
  color: #3776AB;
}

.skill-item:nth-child(7) .skill-icon { /* MySQL */
  color: #4479A1;
}

.skill-name {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 500;
}

/* Education Section Styles */
.education-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  max-width: 900px;
  margin: 0 auto;
}

.education-item {
  display: flex;
  align-items: center;
  gap: 2.5rem;
  padding: 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 12px rgba(112, 112, 255, 0.2);
}

.education-logo-container {
  flex-shrink: 0;
  width: 150px;
  height: 150px;
  border-radius: 10px;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.education-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.education-details {
  flex: 1;
}

.education-title {
  color: #7070FF;
  font-size: 1.6rem;
  margin: 0 0 0.8rem 0;
}

.education-school {
  color: #ffffff;
  font-size: 1.4rem;
  margin: 0 0 0.8rem 0;
}

.education-period {
  color: #cccccc;
  font-size: 1.2rem;
  margin: 0;
}

/* Certifications Section Styles */
.certifications-container {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  max-width: 900px;
  margin: 0 auto;
}

.certification-item {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  padding: 2rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.certification-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 12px rgba(112, 112, 255, 0.2);
}

.certification-title {
  color: #7070FF;
  font-size: 1.6rem;
  margin: 0 0 0.8rem 0;
}

.certification-issuer {
  color: #ffffff;
  font-size: 1.2rem;
  margin: 0 0 0.5rem 0;
}

.certification-date {
  color: #cccccc;
  font-size: 1rem;
  margin: 0 0 1rem 0;
}

.certification-description {
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
}

.certification-link {
  display: inline-block;
  padding: 0.8rem 1.5rem;
  background-color: rgba(112, 112, 255, 0.1);
  color: #7070FF;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.certification-link:hover {
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

/* Projects Section Styles */
.projects-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  max-width: 1000px;
  margin: 0 auto;
}

.project-item {
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  display: flex;
  flex-direction: column;
}

.project-item:hover {
  transform: translateY(-5px);
}

.project-image-container {
  width: 100%;
  aspect-ratio: 16/9;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-content {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  flex: 1;
}

.project-title {
  color: #7070FF;
  font-size: 1.4rem;
  margin: 0 0 1rem 0;
}

.project-description {
  color: #ffffff;
  font-size: 1rem;
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
  flex: 1;
}

.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  justify-content: center;
  margin: auto 0;
  padding: 1rem 0;
  background-color: rgba(112, 112, 255, 0.05);
  border-radius: 0 0 10px 10px;
}

.tech-item {
  padding: 0.5rem 1rem;
  background-color: rgba(112, 112, 255, 0.1);
  border-radius: 20px;
  transition: all 0.3s ease;
}

.tech-item:hover {
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

.tech-name {
  color: #7070FF;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Contact Section Styles */
.contact-info {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 3rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.2rem 2rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: transform 0.3s ease;
  flex: 1;
  max-width: 350px;
  min-width: 300px;
}

.contact-item:hover {
  transform: translateY(-5px);
}

.contact-icon {
  font-size: 1.8rem;
  color: #7070FF;
}

.contact-details h3 {
  color: #7070FF;
  font-size: 1.1rem;
  margin: 0 0 0.3rem 0;
}

.contact-details a {
  color: #ffffff;
  text-decoration: none;
  font-size: 1rem;
  transition: color 0.3s ease;
}

.contact-form {
  max-width: 800px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  text-align: left;
}

.form-group label {
  color: #7070FF;
  font-size: 1rem;
  font-weight: 500;
  text-align: left;
  margin-left: 0.2rem;
}

.form-group input,
.form-group textarea {
  padding: 0.8rem;
  background-color: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  color: #ffffff;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  text-align: left;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #7070FF;
}

.submit-btn {
  padding: 1rem 2rem;
  background-color: #7070FF;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  align-self: flex-start;
}

.submit-btn:hover {
  background-color: #5a5ac7;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .nav-content {
    padding: 0 1rem;
  }
  
  .nav-list {
    display: none; /* Hide the navigation list on mobile */
  }

  .logo-image {
    height: 80px; /* Smaller logo on mobile */
  }
  
  .name {
    font-size: 2.5rem; /* Smaller name on mobile */
  }
  
  .title {
    font-size: 1.4rem; /* Smaller title on mobile */
  }

  .about-section,
  .skills-section,
  .education-section,
  .projects-section,
  .certifications-section,
  .contact-section {
    width: 95%;
    margin: 2rem auto;
    padding: 1.5rem;
  }

  .section-title {
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }

  .about-content {
    flex-direction: column;
    gap: 2rem;
    padding: 0;
  }

  .profile-image-container {
    width: 200px; /* Smaller profile image on mobile */
    height: 200px;
    margin: 0 auto;
  }

  .about-text-container {
    padding: 0;
    max-width: 100%;
  }

  .about-text {
    font-size: 1rem;
    line-height: 1.6;
    text-align: left;
    margin: 0 0 1.5rem 0;
  }

  .social-links {
    flex-direction: column; /* Stack social links on mobile */
    align-items: center;
    gap: 1rem;
  }

  .social-link {
    width: 100%; /* Full width social links */
    justify-content: center;
  }

  .skills-container {
    gap: 1rem;
  }

  .skill-item {
    width: calc(33.33% - 1rem); /* 3 skills per row on mobile */
    padding: 0.8rem;
    min-width: 90px;
  }

  .skill-icon {
    font-size: 2rem;
  }

  .skill-name {
    font-size: 0.8rem;
  }

  .education-item {
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
    gap: 1.5rem;
  }

  .education-logo-container {
    width: 100px;
    height: 100px;
  }

  .education-title {
    font-size: 1.2rem;
  }

  .education-school {
    font-size: 1rem;
  }

  .education-period {
    font-size: 0.9rem;
  }

  .projects-container {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    width: 100%;
  }

  .project-item {
    margin: 0;
  }

  .project-content {
    padding: 1rem;
  }

  .project-title {
    font-size: 1.2rem;
    text-align: center;
  }

  .project-description {
    font-size: 0.9rem;
    text-align: left;
  }

  .project-tech-stack {
    padding: 0.8rem 0;
    flex-wrap: wrap;
    justify-content: center;
  }

  .tech-item {
    padding: 0.4rem 0.8rem;
    margin: 0.2rem;
  }

  .tech-name {
    font-size: 0.8rem;
  }

  .project-link {
    width: 100%; /* Full width project links */
    margin-top: 1rem;
  }

  .contact-info {
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 2rem;
  }

  .contact-item {
    padding: 1rem;
    width: 100%;
  }

  .contact-icon {
    font-size: 1.3rem;
  }

  .contact-details h3 {
    font-size: 1rem;
  }

  .contact-details a {
    font-size: 0.9rem;
  }

  .form-group input,
  .form-group textarea {
    padding: 0.7rem;
    font-size: 0.9rem;
  }

  .submit-btn {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
  }

  .education-container,
  .certifications-container {
    width: 100%;
    padding: 0;
  }

  .education-item,
  .certification-item {
    margin: 0;
    padding: 1rem;
  }

  .contact-form {
    width: 100%;
    padding: 0;
  }

  .form-group {
    margin-bottom: 1rem;
  }
}

@media (max-width: 480px) {
  .name {
    font-size: 2rem;
  }
  
  .title {
    font-size: 1.2rem;
  }

  .profile-image-container {
    width: 180px;
    height: 180px;
  }

  .skill-item {
    width: calc(50% - 1rem); /* 2 skills per row on very small devices */
  }

  .section-title {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }

  .project-image-container {
    aspect-ratio: 4/3; /* Adjust aspect ratio for better mobile viewing */
  }

  .about-section,
  .skills-section,
  .education-section,
  .projects-section,
  .certifications-section,
  .contact-section {
    width: 98%;
    margin: 1.5rem auto;
    padding: 1rem;
  }

  .about-text {
    font-size: 0.9rem;
    line-height: 1.5;
  }

  .education-logo-container {
    width: 80px;
    height: 80px;
  }

  .project-content {
    padding: 0.8rem;
  }

  .project-description {
    font-size: 0.9rem;
    line-height: 1.5;
  }

  .certification-title {
    font-size: 1.2rem;
  }

  .certification-description {
    font-size: 0.9rem;
    line-height: 1.5;
  }

  .contact-item {
    padding: 0.8rem;
  }

  .form-group input,
  .form-group textarea {
    padding: 0.8rem;
    font-size: 0.9rem;
  }
}

/* Add a new breakpoint for very small devices */
@media (max-width: 480px) {
  .name {
    font-size: 2rem;
  }
  
  .title {
    font-size: 1.2rem;
  }

  .profile-image-container {
    width: 180px;
    height: 180px;
  }

  .skill-item {
    width: calc(50% - 1rem); /* 2 skills per row on very small devices */
  }

  .section-title {
    font-size: 1.5rem;
  }

  .project-image-container {
    aspect-ratio: 4/3; /* Adjust aspect ratio for better mobile viewing */
  }
}

/* Add hamburger menu styles */
.nav-toggle {
  display: none;
}

@media (max-width: 768px) {
  .nav-toggle {
    display: block;
    font-size: 1.5rem;
    color: #7070FF;
    background: none;
    border: none;
    cursor: pointer;
    padding: 0.5rem;
  }

  .nav-list.active {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.95);
    padding: 1rem;
    gap: 1rem;
  }
}

/* Animation Keyframes */
@keyframes slideUp {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Apply animations to text elements only */
.name,
.title,
.section-title,
.about-text,
.education-title,
.education-school,
.education-period,
.project-title,
.project-description,
.tech-name,
.contact-details h3,
.contact-details a {
  opacity: 0;
  transform: translateY(50px);
  animation: slideUp 0.8s ease-out forwards;
}

/* Add animation delays for staggered effect */
.name { animation-delay: 0.1s; }
.title { animation-delay: 0.2s; }
.section-title { animation-delay: 0.3s; }
.about-text { animation-delay: 0.4s; }
.education-title { animation-delay: 0.5s; }
.education-school { animation-delay: 0.6s; }
.education-period { animation-delay: 0.7s; }
.project-title { animation-delay: 0.8s; }
.project-description { animation-delay: 0.9s; }
.tech-name { animation-delay: 1s; }
.contact-details h3 { animation-delay: 1.1s; }
.contact-details a { animation-delay: 1.2s; }

/* Remove animations from images */
.profile-image,
.education-logo,
.project-image,
.skill-icon {
  animation: none;
  opacity: 1;
  transform: none;
}

.project-link {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin: 1rem auto 0;
  color: #7070FF;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
  width: fit-content;
  padding: 0.5rem 1rem;
  background-color: rgba(112, 112, 255, 0.1);
  border-radius: 5px;
}

.project-link:hover {
  color: #ffffff;
  background-color: rgba(112, 112, 255, 0.2);
  transform: translateY(-2px);
}

.link-icon {
  font-size: 1.2rem;
}

/* Ensure images don't overflow */
img {
  max-width: 100%;
  height: auto;
}

/* Ensure consistent spacing */
.section-title {
  margin-bottom: 2rem;
}

.project-item,
.education-item,
.certification-item,
.contact-item {
  margin-bottom: 1.5rem;
}

/* Fix container overflow */
.projects-container,
.education-container,
.certifications-container,
.contact-info {
  width: 100%;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
} 